.card {
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  max-width: 800px;
  margin: 40px auto;
  margin-top: 130px;
  height: 300px; /* Set a fixed height for the card */
  overflow-y: hidden; /* Make the card scrollable if content overflows */
}

.title {
  font-size: 24px;
  color: #333;
}

.subtitle {
  font-size: 18px;
  margin-bottom: 20px;
  color: #555;
}

.text {
  font-size: 16px;
  color: #333;
}

.table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.table th,
.table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
}

.table th {
  background-color: #f5f5f5; /* Light gray background for header */
  font-weight: bold;
}

.table td {
  background-color: #fff;
}

.spinner-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;  /* Full height of the viewport */
  flex-direction: column;
  text-align: center;
}

.spinner-container h1,
.spinner-container p {
  margin: 10px 0;
}


@media (max-width: 768px) {
  .card {
    margin-top: 100px;
  }

  .table {
    font-size: 14px;
  }
}
